import React,  {useState, useEffect} from 'react';
import './App.css'
import Header from './Components/Header'
import Sidebar from './Components/Sidebar'
import Contents from './Components/Contents'
import {BrowserRouter as Router, Route} from 'react-router-dom';
import jQuery from 'jquery';
let $ = window.jQuery = jQuery;
function App(){
	const [url,setUrl] = useState('');
	const [sidebaronoff, setsidebaronoff] = useState(1);
	const [title,setTitle] = useState('HOME');
	var sidebarlist=[];
	const [blogcontent,setblogcontent] = useState(sidebarlist);
	$(function(){
		$(".Sidebar").css("left",sidebaronoff*-300);
		(!sidebaronoff)? $(".Shadow").css("box-shadow","0 0 0 9999px rgba(0,0,0,0.5)"): $(".Shadow").css("box-shadow","0 0 0 9999px rgba(0,0,0,0)")

	})
	
	return (
		<div>
			<Header k={setUrl} sidebaronoff={sidebaronoff} setsidebaronoff={setsidebaronoff} title={title}/>
			<Sidebar sidebaronoff={sidebaronoff} setsidebaronoff={setsidebaronoff} blogcontent={blogcontent}/>
			<Contents url={url} setTitle={setTitle} setblogcontent={setblogcontent}/>
		</div>
	)
}
export default App;