import React from 'react'
import img1 from './Public/1.jpg'
import img2 from './Public/2.jpg'
import img3 from './Public/3.jpg'
import img4 from './Public/4.jpg'
import img5 from './Public/5.jpg'
import img6 from './Public/6.jpg'
import img7 from './Public/7.jpg'
import img8 from './Public/8.jpg'
import img9 from './Public/9.jpg'
import img10 from './Public/10.jpg'
import img11 from './Public/11.jpg'
import img12 from './Public/12.jpg'
import img13 from './Public/13.jpg'
import img14 from './Public/14.png'
import img15 from './Public/15.jpg'
import './Element.css'
function Element(props) {
    function propstoimg(k){
        switch(k){
            case 1:
                return img1;
            case 2:
                return img2;
            case 3:
                return img3;
            case 4:
                return img4;
            case 5:
                return img5;
            case 6:
                return img6;
            case 7:
                return img7;
            case 8:
                return img8;
            case 9:
                return img9;
            case 10:
                return img10;
            case 11:
                return img11;
            case 12:
                return img12;
            case 13:
                return img13;
            case 14:
                return img14;
            case 15:
                return img15;
            
        }
    }
    return(
        <div className="masonry-item">
           <img src={propstoimg(props.num)}/>
        </div>
    )
}
export default Element