import React from 'react';
import { BrowserRouter as Router, Route,Switch} from 'react-router-dom';
import './Style/Contents.css'
import Home from './Pages/Home';
import Blog from './Pages/Blog';
import Gallery from './Pages/Gallery'
function Contents(props){
    let currentPage = null;
    switch(props.url){
        case process.env.PUBLIC_URL + '/':
            currentPage = <Home setTitle={props.setTitle}/>;
            break;
        case process.env.PUBLIC_URL + '/home':
            currentPage = <Home setTitle={props.setTitle}/>;
            break;
        case process.env.PUBLIC_URL + '/blog':
            currentPage = <Blog setTitle={props.setTitle} setblogcontent={props.setblogcontent}/>;
            break;
        case process.env.PUBLIC_URL + '/gallery':
            currentPage = <Gallery setTitle={props.setTitle}/>;
            break;
    }
    let currentUrl = null;
    if(props.url == ''){
        currentUrl =(
        <Router>
            <Switch>
            <Route exact path={process.env.PUBLIC_URL+'/'} render={() => <Home setTitle={props.setTitle} />}/>
            <Route exact path={process.env.PUBLIC_URL+'/home'} render={() => <Home setTitle={props.setTitle} />}/>
            <Route exact path={process.env.PUBLIC_URL+'/blog'} render={() => <Blog setTitle={props.setTitle} setblogcontent={props.setblogcontent}/>}/>
            <Route exact path={process.env.PUBLIC_URL+'/gallery'} render={() => <Gallery setTitle={props.setTitle} />}/>
            </Switch>
        </Router>
        )
    }
    return (
        <div>
            {currentPage}
            {currentUrl}
           
        </div>
    )
}
export default Contents;