import React from 'react'
import Element from './Gallery/Element.js'
import './default.css'
function Gallery(props) {
    props.setTitle('GALLERY');
    let img=[];
    for(let i=1; i<=15; i++){
        img.push(<Element num={i} />);
    }
    return(
        <div className="masonry-wrapper">
            <div className="masonry">
                {img}
            </div>
        </div>
    )
}
export default Gallery;