import React from 'react'
import './Style/Sidebar.css'
import profile from '../profile.jpg'
function Sidebar(props) {
    
    return (
        <div>
        <div className="Sidebar">
            <div className="Sidebar-wrapper">
                <div className="img-wrapper"><img src={profile}/></div>
                <div className="title-text">-kimtahen-</div>
    
                <div className="text">facebook</div>
                <a href="https://www.facebook.com/kimtahen">https://www.facebook.com/kimtahen</a>
                <div className="text">instagram</div>
                <a href="https://www.instagram.com/km_t_hn">https://www.instagram.com/km_t_hn</a>
                {props.blogcontent}
            </div>
        </div>
        <div className="Shadow" ></div>
        </div>
    )
}
export default Sidebar