import React,{useState} from 'react'
import {
    BrowserRouter as Router,
    Route,
    Switch,
    Link
  } from 'react-router-dom'
import './Style/Header.css'
function Header(props){
    const[button,setbutton]=useState(1);
    let sidebutton;
    sidebutton = (button)? '>>' : '<<';
    return (
        <Router>
        <div className="Header">
            <div className="toggle" onClick={()=>{props.setsidebaronoff(!props.sidebaronoff); setbutton(!button)}}>
            <span>{sidebutton}</span>
            </div>
            <div className="title">{props.title}</div>            
            <div className="router wrapper">
                <div className="router content">
                    <Link to={process.env.PUBLIC_URL + '/home'} onClick={()=>{props.k(process.env.PUBLIC_URL+'/home')}}>Home</Link>
                </div>
                <div className="router content">
                    <Link to={process.env.PUBLIC_URL + '/blog'} onClick={()=>{props.k(process.env.PUBLIC_URL+'/blog')}}>Blog</Link>
                </div>
                <div className="router content">
                    <Link to={process.env.PUBLIC_URL + '/gallery'} onClick={()=>{props.k(process.env.PUBLIC_URL+'/gallery')}}>Gallery</Link>
                </div>
                <div className="router content">
                    <a href="https://github.com/kimtahen">GitHub</a>
                </div>
            </div>
        </div>
        </Router>
    )
}
export default Header;